.iconNameTimesEditContainer {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 1rem;
  align-items: flex-start;
}

.buttonsTimesContainer{
  display: flex;
}

button.deleteIngredientButton {
  border: none;
  font-size: 0.9rem;
}

.RecipeURLContainer {
  width: 50%;
  margin-left: auto;
  text-align: left;
  padding-left: 1.6vw;
  padding-right: 2.5vw;
}

.editDeleteButtonContainer {
  display: flex;
  flex-direction: column;
  margin-right: 1.25vw;
  margin-left: .5vw;
}

.noBorderLessPaddingBtn{
  padding: .2rem .2rem;
  border: none;

}

div.RecipeURLAndAddButtonContainer > button.noBorder {
  transform: none;
  font-size: 1.5rem;

}
div.editDeleteButtonContainer > div.deleteButtonContainer > button.noBorderLessPaddingBtn {
  font-size: 1rem;
}

div.editDeleteButtonContainer > button.noBorderLessPaddingBtn > i.fa-solid.fa-pencil {
  font-size: 1.1em;
}

